/*Ispired by Andreas Storm's text input https://freefrontend.com/css-input-text/ */
/* Customized to fit and work with this project */

.searchbar {
    font-family: 'Helvetica';
    color: white !important;
    margin-top: 5px;
    margin-left: 15px;
    font-size: 20px !important;
}

 .inp {
     position: relative;
     margin: auto;
     width: 100%;
     max-width: 280px;
     padding: 20px;
}
 .inp .label {
     position: absolute;
     top: 16px;
     left: 0;
     font-size: 26px;
     color: #fff707e7;
     font-weight: 500;
     -webkit-transform-origin: 0 0;
         -ms-transform-origin: 0 0;
             transform-origin: 0 0;
     -webkit-transition: all 0.2s ease;
     -o-transition: all 0.2s ease;
     transition: all 0.2s ease;
}
 .inp svg {
     position: absolute;
     left: 0;
     bottom: 0;
     height: 26px;
     fill: none;
}
 .inp svg path {
     stroke: #0732a2;
     stroke-width: 2;
}
 .inp svg path d {
     -webkit-transition: all 0.2s ease;
     -o-transition: all 0.2s ease;
     transition: all 0.2s ease;
}
 .inp .border {
     position: absolute;
     bottom: 0;
     left: 120px;
     height: 2px;
     right: 0;
     background: #0732a2;
}
 .inp input {
     -webkit-appearance: none;
     width: 100%;
     border: 0;

     padding: 12px 0;
     height: 48px;
     font-size: 16px;
     font-weight: 500;
     background: none;
     border-radius: 0;
     color: #ffda07e7;
     -webkit-transition: all 0.15s ease;
     -o-transition: all 0.15s ease;
     transition: all 0.15s ease;
}
 .inp input:not(:placeholder-shown) + span {
     color: #ffda07e7;
     -webkit-transform: translateY(-26px) scale(0.75);
         -ms-transform: translateY(-26px) scale(0.75);
             transform: translateY(-26px) scale(0.75);
}
 .inp input:focus {
     background: none;
     outline: none;
}
 .inp input:focus + span {
     color: #ffda07e7;
     -webkit-transform: translateY(-26px) scale(0.75);
         -ms-transform: translateY(-26px) scale(0.75);
             transform: translateY(-26px) scale(0.75);
     -webkit-transition-delay: 0.1s;
          -o-transition-delay: 0.1s;
             transition-delay: 0.1s;
}
 .inp input:focus + span + svg path {
     stroke: #ffda07e7;
     -webkit-animation: elasticInput 0.4s ease forwards;
             animation: elasticInput 0.4s ease forwards;
}
 .inp input:focus + span + svg + .border {
     background: #ffda07e7;
}
 .dribbble {
     position: fixed;
     right: 20px;
     bottom: 15px;
     -webkit-transition: all 0.2s ease;
     -o-transition: all 0.2s ease;
     transition: all 0.2s ease;
}
 .dribbble span {
     font-size: 28px;
     color: #ffda07e7;
}
 @-webkit-keyframes elasticInput {
     50% {
         d: path("M2,2 C21,17 46,25 74,25 C102,25 118,25 120,25");
    }
}
 @keyframes elasticInput {
     50% {
         d: path("M2,2 C21,17 46,25 74,25 C102,25 118,25 120,25");
    }
}


